.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;  
}

 .MuiCardContent-root {background-color:#eef7b8 !important;}

.card {
  margin: 0 2% !important;
  background-color: #eef7b8;
  
}

.CardMedia{background-image:url('/src/images/bg1.jpg');}
.infected {
  border-bottom: 10px solid rgba(0, 0, 255, 0.5);
  background-color: #eef7b8;
}

.recovered{
  border-bottom: 10px solid rgba(0, 255, 0, 0.5);
}

.deaths{
  border-bottom: 10px solid rgba(255, 0, 0, 0.5);
}


@media only screen and (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
 
}